<template>
  <div>
    <div class="card bg-light mb-0">
      <div class="card-header bg-dark text-white d-flex justify-content-between align-items-center">
          <h6 class="mb-0"><font-awesome-icon icon="cubes" /> Material Inventory</h6>
          <b-button variant="secondary" size="sm" @click="modal=true">
              <font-awesome-icon icon="plus" /> Add New
          </b-button>
      </div>
      <div class="card-body bg-white position-relative">
        <b-row align-v="center" class="loader" v-if="is_saving">
            <b-col>
                <b-spinner></b-spinner>
                <br><br>
                <strong>Loading</strong>
            </b-col>
        </b-row>
        <b-row>
            <b-col cols="3" class="bg-light text-dark p-3 ml-3">
                <b-form @submit.prevent="save">

                  <b-form-group
                    label="Remarks:"
                    description="This field is required"
                  >
                    <textarea class="form-control" required v-model="form.remarks"></textarea>
                  </b-form-group>

                  <div class="text-right">
                    <b-button type="submit" variant="dark" size="sm"><font-awesome-icon icon="save" /> Save</b-button>
                  </div>
                </b-form>
            </b-col>
            <b-col class="px-3">
              <table class="table border mb-0">
                <thead class="bg-light text-dark">
                  <tr>
                    <th scope="col">Material</th>
                    <th scope="col" class="text-center">Price</th>
                    <th scope="col" class="text-center">Qty</th>
                    <th scope="col" class="text-center">Action </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(starting_inventory_detail, index) in form.starting_inventory_details">
                    <th scope="row">{{starting_inventory_detail.material.name}}</th>
                    <td>
                      <b-form-input
                          v-model="starting_inventory_detail.price"
                          type="number"
                          step="any"
                          required
                        ></b-form-input>
                    </td>
                    <td>
                      <b-form-input
                          v-model="starting_inventory_detail.qty"
                          type="number"
                          step="any"
                          required
                        ></b-form-input>
                    </td>
                    <td class="text-center">
                      <b-button type="submit" size="sm" variant="danger" @click="deleteMaterial(index)"><font-awesome-icon icon="minus-circle" /> Remove</b-button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </b-col>
        </b-row>
      </div>
    </div>
    <b-modal v-model="modal" centered size="xl" body-class="p-0" :hide-footer="true" :hide-header="true">
      <div class="card bg-light mb-0">
          <div class="card-header bg-dark text-white">
              <h6 class="mb-0"><font-awesome-icon icon="plus-circle" /> Add New Item</h6>
          </div>
          <div class="card-body bg-white p-2">
            <b-row align-v="center" class="loader" v-if="is_saving">
                <b-col>
                    <b-spinner></b-spinner>
                    <br><br>
                    <strong>Loading</strong>
                </b-col>
            </b-row>
            <b-row class="m-0 border mb-2">
              <b-col cols="1" class="text-center p-1 bg-success text-white d-flex align-items-center justify-content-center">
              <font-awesome-icon icon="search" class="h3 mb-0" />
              </b-col>
              <b-col class="p-2 bg-light">
                  <input  type="text" 
                          class="form-control" 
                          placeholder="Search..." 
                          v-model="search_key"
                          debounce="500"
                        />
              </b-col>
            </b-row>
            <b-row class="m-0">
              <b-col cols="6" class="p-2 bg-light">
                  <b-alert show variant="danger" v-if="materials.length<=0">No search result/s found.</b-alert>
                  <div class="list-group bg-white mt-2" style="max-height:260px;overflow-y: auto;" v-else>
                      <a href="#" class="list-group-item list-group-item-action" v-for="material in materials" @click.prevent="select(material)">{{material.name}}</a>
                  </div>
              </b-col>
              <b-col class="p-0 pl-2">
                <b-form @submit.prevent="addMeterial">
                  <b-row class="m-0 border mb-3">
                      <b-col cols="2" class="text-center p-2 bg-info text-white d-flex align-items-center justify-content-center">
                          <font-awesome-icon icon="cube" class="h2 mb-0" />
                      </b-col>
                      <b-col class="p-2 bg-white d-flex align-items-center">
                          <div>
                          <h6 class="mb-0 text-muted">Material</h6>
                          <h2 class="mb-0">
                            <strong>
                              <template v-if="typeof starting_inventory_detail.material.name != 'undefined'">
                                {{starting_inventory_detail.material.name}}
                              </template>
                              <template v-else>
                                .....
                              </template>
                            </strong>
                          </h2>
                          </div>
                      </b-col>
                  </b-row>

                  <b-form-group
                    label="Price:"
                    description="This field is required"
                  >
                    <b-form-input
                      v-model="starting_inventory_detail.price"
                      type="number"
                      step="any"
                      required
                    ></b-form-input>
                  </b-form-group>

                  <b-form-group
                    label="Qty:"
                    description="This field is required"
                  >
                    <b-form-input
                      v-model="starting_inventory_detail.qty"
                      type="number"
                      step="any"
                      required
                    ></b-form-input>
                  </b-form-group>


                  <div class="text-right">
                    <b-button type="submit" variant="dark" size="sm"><font-awesome-icon icon="save" /> Add</b-button>
                  </div>
                </b-form>
              </b-col>
            </b-row>
          </div>
        </div>
    </b-modal>
  </div>
</template>
<script>
    import SwalMixin from '@/views/mixins/Swal.js'
    import NumberFormatComponent from '@/views/helpers/NumberFormatComponent.vue';
    // import Modal from './Modal.vue';
    export default {
      mixins:[SwalMixin],
      components: {NumberFormatComponent},
      props:{
          project_location_structure_item: {
              type: Object,
              default: () => {},
              required:false
          }
      },
      data() {
        return {
          is_saving:false,
          modal:false,
          form:{
            id:-1,
            project_location_structure_item_id:this.$route.params.project_location_structure_item_id,
            remarks:'',
            starting_inventory_details:[]
          },
          starting_inventory_detail:{
            id:-1,
            material_id:-1,
            material:{},
            price:'',
            qty:''
          },
          materials:[],
          search_key:'',
        }
      },
      watch: {
        project_location_structure_item:{
            immediate: true,
            deep:true,
            handler(){
                //this.get();
            }
        },
        search_key:{
            immediate: true,
            handler(){
                if(this.search_key!='')
                this.getMaterials();
            }
        }
      },
      created(){
      },
      methods: {
        getMaterials(){
            this.axios.get('maintenance/materials/search?search=' + this.search_key).then(response => {
                this.materials=response.data
            }).catch(error => console.log(error));
        },
        select(material){
            this.starting_inventory_detail.material=material;
            this.starting_inventory_detail.material_id = material.id;
        },
        save(){
          this.swalConfirmSubmit((data)=>{
              if (data.value) {
                this.modal_is_saving=true;
                this.axios.post('inventory/reset',this.form).then(response => {
                    this.swalTransactionCompleted();
                    this.is_saving=false;
                    //this.$emit('addEquipment');
                }).catch(error => {
                    this.swal_transaction_error_data.text = error.response.data;
                    this.swalRequestError();
                    this.is_saving=false;
                });
              }
          });
        },
        addMeterial(){
          if(this.starting_inventory_detail.material_id==-1){
            this.swal_transaction_error_data.text = "Please select a material.";
            this.swalRequestError();
            return
          }
          let data = JSON.parse(JSON.stringify(this.starting_inventory_detail));
          
         if(this.form.starting_inventory_details.find(x => (x.material_id === data.material_id && x.price==data.price)))
         {
            this.swal_transaction_error_data.text = "Material with same price already exist.";
            this.swalRequestError();

         }else{
          data.material_id = data.material.id;
          this.form.starting_inventory_details.push(data);
          this.modal=false
         }

        },
        deleteMaterial(index){
          this.swalConfirmDelete((data)=>{
              if (data.value) {
                this.form.starting_inventory_details.splice(index, 1);
              }
          });
        }
      }
    }
</script>
